//React component that stores the tutor accout processing for users
import React from 'react';
// import "./AnswerPage.css";
import {FaHome} from 'react-icons/fa';
import Axios from 'axios';
// import PermanentDrawerLeft from "./TutorAnsweringView";
import AnswerThreadView from "./AnswerThreadView";

//getting the question information from the table that was stored in the localstorage
//STUDENT - QUESTION INFORMATION
const topic = localStorage.getItem("topic");
const description = localStorage.getItem("description");
const studentName = localStorage.getItem("studentName");
const professor = localStorage.getItem("professor");
const course = localStorage.getItem("courseName");
const timeStamp = localStorage.getItem('timeStamp');
const status = localStorage.getItem('status');
const questionID = localStorage.getItem('questionID');
const studentID = localStorage.getItem('student_ID');
// console.log(studentID);
//TUTOR INFORMATION:
const tutorName = localStorage.getItem('tutor_name');
const tutor_id = localStorage.getItem('tutor_id');
// console.log(tutorName)
// console.log(tutor_id)

// console.log(questionInfo);
class AnswerPage extends React.Component{
    render(){
        return(
            <div className="show-data-tutor">
                <div className="navBar_01">
                    <FaHome />
                </div>
                <div className="question-info">
                    <div className="Topic">
                        <h3>{topic}</h3>

                        <div className="Course">
                            Course: <span>{course}</span>
                        </div>
                    </div>
                    <div className="Description">
                            <span>Description:</span>
                            <br></br> 
                            <p>{description}</p>
                    </div>
                    <div className="case-info">
                        <div className="info1">
                            <div className="student-name">
                                <span>Student Username: </span>
                                <p>{studentName}</p>
                            </div>
                            <div className="prof-name">
                                    <span>Professor Name: </span>
                                    <p>{professor}</p>
                            </div>
                        </div>
                        <div className="info2">
                            <div className="time-stamp">
                                <span>Recieved at: </span>
                                <p>{timeStamp}</p>
                            </div>
                            <div className="Status">
                                <span>Status: </span>
                                <p>{status}</p>
                            </div>
                        </div>
                    </div>
                </div>
                <AnswerThreadView />
                <div className="answer-question">
                    <form onSubmit={(e) => sendAnswer(e)}>
                        <div className="answer-box">
                            <textarea className="answerBox" id="explanation" placeholder="Enter your resolution of the issue..."></textarea>
                        </div>
                        <div className="sendAns">
                                <button type="submit" className="btn">Submit Response</button>
                        </div>
                    </form>
                </div>
                
            </div>
        )
    }
}

//Function to send the answer to the user 
function sendAnswer(e){
    e.preventDefault();
    const reply_TIME = new Date();
    const reply_TIME_FORMATTED = new Intl.DateTimeFormat('en-US', {year: 'numeric', month: '2-digit',day: '2-digit', hour: '2-digit', minute: '2-digit', second: '2-digit'}).format(reply_TIME);
    const answer = document.getElementById('explanation').value
    const status = "pending"
    console.log(answer);

    //Setting up a request: 
    let request = {
        tutorID: tutor_id,
        tutorName: tutorName,
        questionID: questionID,
        answer: answer,
        timestamp: reply_TIME_FORMATTED,
        studentID: studentID,
        status: status
    }

    //Axio command to connect to the back end via the api url:
    Axios.post('http://206.189.194.211:8080/answer', request)
    //server response handling: 
    .then(response => {
        console.log("Server response...");
        alert("Replies sent to user...")
    })

    .catch(err => {
        console.log(err);
        console.log(request);
    });
}

export default AnswerPage;
